<template>
    <div class="book">
        <db-head></db-head>
        <section>
            <db-section v-if="!loading" :data="bookData.book" :size="size"></db-section>
            <db-foot></db-foot>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbFoot from "../components/commons/dbFoot";
    import dbSection from "../components/dbMusic/dbNewcd";
    import bookApi from "../apis/bookApi";
    export default {
        name: "dbBook",
        components:{
            dbHead,
            dbFoot,
            dbSection
        },
        data(){
            return{
                loading:false,
                bookData:[],
                size:''
            }
        },
        created(){
            this._initPageData()
            this.getSize()
        },
        methods:{
            _initPageData(){
                this.loading = true;
                bookApi.getPush(data=>{
                    this.bookData = data;
                    console.log(this.bookData);
                    this.loading = false;
                })
            },
            getSize(){
                if(this.bookData.title ="bigSize"){
                    this.size = 150;
                }
            }
        }
    }
</script>

<style scoped>
    .book{
        height:100%;
        background-color: white;
        display: flex;
        flex-direction: column;
    }
    section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }
</style>


