<template>
    <div class="dbIndex">
        <db-head></db-head>
        <div class="section">
            <db-index-nav></db-index-nav>
            <db-index-list :data="indexData"></db-index-list>
        </div>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbIndexNav from "../components/dbIndex/dbIndexNav";
    import dbIndexList from "../components/dbIndex/dbIndexList";
    import indexApi from "../apis/indexApi";

    export default {
        name: "dbIndex",
        components: {
            dbHead,
            dbIndexNav,
            dbIndexList
        },
        data(){
            return {
                indexData :[]
            }
        },
        created(){
            this._initPageData()
        },
        methods: {
            _initPageData(){
                indexApi.getPush(data=>{
                    this.indexData = data;
                    //console.log(this.indexData);
                })
            }
        }
    }
</script>

<style scoped>
    .dbIndex{
        height:100%;
        display: flex;
        flex-direction: column;
    }
    .section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }
</style>


