<template>
    <div class="movie">
        <db-head></db-head>
        <section>
            <db-mlist :data="movieData"></db-mlist>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbMlist from "../components/dbMovie/dbMlist";
    import movieApi from "../apis/movieApi";
    export default {
        name: "dbMovie",
        components:{
            dbHead,
            dbMlist
        },
        data(){
            return {
                movieData:[]
            }
        },
        created() {
            this._initPageData()
        },
        methods: {
            _initPageData(){
                movieApi.getPush(data=>{
                    this.movieData = data;
                    console.log(this.movieData)
                })
            }
        }
    }
</script>

<style scoped>
    .movie{
        height:100%;
        background-color: white;
        display: flex;
        flex-direction: column;
    }
    section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }
</style>


