<template>
    <div class="nav">
        <div class="nav-top my">
            <a href="javascript:;" @click="go">知野</a>
            <a href="javascript" style="border-left: 1px solid #e5e5e5">退出豆瓣</a>
        </div>
        <div class="nav-down my">
            <a href="javascript:;">使用桌面版</a>
            <a href="javascript" style="border-left: 1px solid #e5e5e5">使用豆瓣App</a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dbSeachNav",
        methods: {
            go(){
                this.$router.push({
                    path: "/my",
                    auery: ""
                })
            }
        }
    }
</script>

<style scoped>
    .my{
        width:100%;
        height:.22rem;
        border-bottom: 1px solid #f3f3f3;
        display: flex;
        padding:.13rem 0;
    }
    a{
        margin: 0 auto;
        text-decoration: none;
        font-size: 15px;
        color:#42bd56;
        flex: 1;
        text-align: center;
    }
</style>


