<template>
   <div class="find">
       <ul>
           <li :style="{borderColor: n.color}" v-for="(n,i) in data" v-if="i<4">
               <a href="javascript:;" :style="{color:n.color}">{{n.title}} </a>
           </li>
       </ul>
       <ul>
           <li :style="{borderColor: n.color}" v-for="(n,i) in data" v-if="i>3">
               <a href="javascript:;" :style="{color:n.color}">{{n.title}}</a>
           </li>
       </ul>
   </div>
</template>

<script>
    export default {
        name: "dbFind",
        props:["data"]
    }
</script>

<style scoped>
    .find{
        overflow-x: auto;
    }
    ul{
        white-space: nowrap;

        padding: 0px 15px 0px 15px;
        font-size: 0;
        margin-left: -5px;
    }
    li{
        margin: 0 0 8px 8px;
        font-size: .94rem;
        display: inline-block;
        border-radius: .04rem;
        border: solid 1px;
        vertical-align: middle;
    }
    a{
        height: .5rem;
        line-height: .5rem;
        padding: 0 .25rem;
        letter-spacing: .1em;
        overflow: auto;
        display: block;
        text-align: center;
        font-size: .14rem;
    }
</style>


