<template>
    <div class="CityList">
        <db-city-head :data="data"></db-city-head>
        <db-city-content v-for="n in data.content" :key="n.id" :data="n"></db-city-content>
    </div>
</template>

<script>
    import dbCityHead from "./dbCityHead";
    import dbCityContent from "./dbCityContent";
    export default {
        name: "dbCityList",
        components:{
            dbCityHead,
            dbCityContent
        },
        props:["data"]
    }
</script>

<style scoped>
    .CityList{
        padding:0 .1rem 0 .1rem;
    }
</style>


