<template>
    <div class="items">
        <img :src="data.pic">
        <div class="mess">
            <p>
                {{data.title}}
            </p>
            <span class="time">{{data.time}} </span>
            <span class="add">
                {{data.add}}
              </span>
            <span class="gz">
                {{data.gz}}
              </span>
        </div>
    </div>

</template>

<script>
    export default {
        name: "dbCityContent",
        props:["data"]
    }
</script>

<style scoped>
    .items{
        padding:.23rem 0 .08rem 0;
        display: flex;
    }
    img{
        width:.7rem;
        height:.9rem;
    }
    .mess{
        padding:0 .1rem 0 .1rem;
    }
    p{
        width:2.6rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        font-size: .13rem;
        text-indent: .06rem;
        padding-bottom: .15rem;
    }
    span{
        font-size:.11rem;
        color:#9b9b9b;
    }
    .add{
        width:1.65rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
</style>


