<template>
    <div class="slider">
        <a href="javascript:;" v-for="n in data">
            <img :src="n">
        </a>
    </div>
</template>

<script>
    export default {
        name: "dbGroupSlider",
        props:["data"]
    }
</script>

<style scoped>
    .slider{
        margin:.25rem .18rem .25rem .18rem;
        display: flex;
        overflow-x: auto;
    }
    a{
        width:1.9rem;
        height:.8rem;
        margin-right: .1rem;
        flex-shrink: 0;
    }
    img{
        width:100%;
        border-radius: .05rem;
    }
</style>


