import Vue from 'vue'
import Router from 'vue-router'
import dbIndex from "../pages/dbIndex";
import dbSeach from "../pages/dbSeach";
import dbMy from "../pages/dbMy";
import dbPeople from "../pages/dbPeople";
import dbMovie from "../pages/dbMovie";
import dbTime from "../pages/dbTime";
import dbMusic from "../pages/dbMusic";
import dbWatch from "../pages/dbWatch";
import dbBook from "../pages/dbBook";
import dbGroup from "../pages/dbGroup";
import dbCity from "../pages/dbCity";

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            redirect:'/index'
        },
        {
          path: '/index',
          name: 'index',
          component: dbIndex
        },
        {
            path: '/seach',
            name:'seach',
            component: dbSeach
        },
        {
            path: '/my',
            name: 'my',
            component: dbMy
        },
        {
            path: '/people',
            name: 'people',
            component: dbPeople
        },
        {
            path:'/movie',
            name:'movie',
            component: dbMovie
        },
        {
            path:'/time',
            name:'time',
            component: dbTime
        },
        {
            path:'/music',
            name:'music',
            component:dbMusic
        },
        {
            path:'/watch',
            name:'watch',
            component:dbWatch
        },
        {
            path:'/book',
            name:'book',
            component:dbBook
        },
        {
            path:'/group',
            name:'group',
            component:dbGroup
        },
        {
            path:'/city',
            name:'city',
            component:dbCity
        }
    ]
})



