<template>
    <div class="city">
        <db-city-list v-if="!loading" v-for="n in cityData.item" :key="n.id" :data="n"></db-city-list>
    </div>
</template>

<script>
    import dbCityList from "../components/dbCity/dbCityList";
    import cityApi from "../apis/cityApi";
    export default {
        name: "dbCity",
        components:{
            dbCityList
        },
        data(){
            return{
                loading:false,
                cityData:[]
            }
        },
        created() {
            this._initPageData()
        },
        methods:{
            _initPageData(){
                this.loading = true;
                cityApi.getPush(data=>{
                    this.cityData = data;
                    this.loading = false;
                    console.log(this.cityData);
                })
            }
        }
    }
</script>

<style scoped>

</style>


