<template>
    <div class="people">
        <db-head></db-head>
        <div class="secBox">
            <section>
                <db-banner v-if="!loading" :data="peopleData.users"></db-banner>
                <db-concern v-if="!loading" :data="peopleData.users"></db-concern>
            </section>
            <div>
                <db-life v-if="!loading" :data="peopleData.users"></db-life>
            </div>
            <db-foot></db-foot>
        </div>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbFoot from "../components/commons/dbFoot";
    import dbBanner from "../components/dbPeople/dbBanner";
    import dbConcern from "../components/dbPeople/dbConcern";
    import dbLife from "../components/dbPeople/dbLife";
    import peopleApi from "../apis/peopleApi";
    export default {
        name: "dbPeople",
        components: {
            dbHead,
            dbFoot,
            dbBanner,
            dbConcern,
            dbLife
        },
        data(){
            return{
                loading: false,
                peopleData:[]
            }
        },
        created() {
            this._initPageData()
        },
        methods: {
            _initPageData(){
                this.loading = true;
                peopleApi.getPush(data=>{
                    this.peopleData = data;
                    this.loading = false;
                    console.log(this.peopleData);
                })
            }
        }
    }
</script>

<style scoped>
    .people{
         height:100%;
         background-color: #f5f5f5;
         display: flex;
         flex-direction: column;
    }
    .secBox{
         flex: 1;
         overflow-x: hidden;
         overflow-y: auto;
    }
</style>


