<template>
    <section class="movieList">
        <h1>影院热映</h1>
        <div class="list">
            <a href="javascript:;" v-for="(n,i) in data.movies">
                <img :src="n.pic" class="m-pic">
                <span class="name">{{n.name}}</span>
                <!--一星-->
                <span class="pf" v-show="n.start===1?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--二星-->
                <span class="pf" v-show="n.start===2?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--三星-->
                <span class="pf" v-show="n.start===3?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--四星-->
                <span class="pf" v-show="n.start===4?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--五星-->
                <span class="pf" v-show="n.start===5?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--无评分-->
                <span class="pf" v-show="n.start===0?true:false" style="display: none">
                    {{n.pf}}
                </span>
            </a>
        </div>
    </section>
</template>

<script>
    import iconfont from "../../assets/js/iconfont";
    export default {
        name: "dbMlist",
        props: ["data"]
    }
</script>

<style scoped>
    .icon {
        width: .8em;
        height: .8em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }
    .movieList h1{
        font-size: .22rem;;
        padding:.18rem 0 .3rem .22rem;
        font-weight: normal;
    }
    .list{
        display: flex;
        flex-wrap: wrap;
        margin-left: .07rem;
    }
    .list a{
        text-align: center;
        padding: 0 .15rem;
        margin-bottom: .25rem;
    }
    .name{
        font-size: .13rem;
        color:#494949;
        max-width: 92px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }
    .pf{
        font-size: .12rem;
        color:#9c9c9c;
    }
    .m-pic{
        width:.91rem;
        height:1.3rem;
    }
</style>


