<template>
    <div class="cityHead">
        <h3>{{data.name}}</h3>
        <ul >
            <li>
                <a href="javascript" class="one">最热</a>
            </li>
            <li v-for="n in data.nav">
                <a href="javascript">{{n}}</a>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "dbCityHead",
        props:["data"]
    }
</script>

<style scoped>
    .cityHead{
        display: flex;
        height:.41rem;
        align-items: center;
        border-bottom: 1px solid #dfdfdf;
    }
    h3{
        font-size:.14rem;
        font-weight: normal;
        flex-shrink: 0;
        padding-right:.06rem;
    }
    ul{
        display: flex;
        overflow-x: auto;
    }
    li{
        display: flex;
        padding-right: .10rem;
        flex-shrink: 0;
    }
    a{
        padding:.05rem .1rem .05rem .1rem;
        font-size: .12rem;
        border: 1px solid #67ba62;
        color:#67ba62;
        border-radius: 3px;
    }
    .one{
        background-color: #67ba62;
        color:white;
    }
</style>


