<template>
    <div>
        <db-newcd-class v-for="n in data" :key="n.id" :data="n" :size="size" ></db-newcd-class>
    </div>
</template>

<script>
    import dbNewcdClass from "./dbNewcdClass";
    export default {
        name: "dbNewcd",
        components:{
            dbNewcdClass
        },
        props:["data","size"]
    }
</script>

<style scoped>

</style>


