<template>
    <ul class="teamList">
        <li>
            <div class="rig">
                <img :src="data.pic">
                <div class="mess">
                    <h3>{{data.title}}</h3>
                    <span>{{data.content}}</span>
                </div>
            </div>
            <div class="add" v-show="data.add?true:false" style="display: none">
                {{data.add}}
            </div>
        </li>
    </ul>
</template>

<script>
    export default {
        name: "dbGroupContent",
        props:["data"]
    }
</script>

<style scoped>
    li{
        display: flex;
        border-top: 1px solid #e8e8e8;
        padding:.14rem 0;
        justify-content: space-between;
    }
    .rig{
        display: flex;
    }
    img{
        width:.36rem;
        height:.36rem;
        margin-right:.1rem;
    }
    h3{
        font-size:.14rem;
        padding-bottom: .02rem;
        font-weight: 500;
        width:2rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    span{
        font-size:.12rem;
        color:#cecece;
    }
    .add{
        padding:.06rem .15rem;
        border:1px solid #67ba62;
        border-radius:5px;
        font-size: .14rem;
        color:#67ba62;
        margin-right: .18rem;
    }
</style>


