/*
使用mockJS提供mock数据接口
*/
import Mock from "mockjs";
import data from './data.json';

//返回pageIndex的接口
//Mock.mock('/index', {code:0, data: data.index});

Mock.mock('/index',function (options) {
    console.log(options)
    return Mock.mock({code:0, data: data.index})
})


