<template>
    <div class="items">
        <div class="mCd" v-for="n in data" :key="n.id">
            <a href="javascript:;">
                <img :src="n.pic" :style="{height:size+'px'}"   >
                <h3>{{n.title}}</h3>
                <!--一星-->
                <span class="pf" v-show="n.start===1?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--二星-->
                <span class="pf" v-show="n.start===2?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--三星-->
                <span class="pf" v-show="n.start===3?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--四星-->
                <span class="pf" v-show="n.start===4?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf2"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--五星-->
                <span class="pf" v-show="n.start===5?true:false" style="display: none">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-pf1"></use>
                    </svg>
                    {{n.pf}}
                </span>
                <!--无评分-->
                <span class="pf" v-show="n.start===0?true:false" style="display: none">
                    {{n.pf}}
                </span>
            </a>
        </div>
    </div>
</template>

<script>
    import iconfont from "../../assets/js/iconfont";
    export default {
        name: "dbNewcdDvd",
        props:["data","size"]
    }
</script>

<style scoped>
    .items{
        display: flex;
        flex-wrap: nowrap;
        overflow-y: auto;
        padding-left: .11rem;
    }
    .mCd{
        padding:0 .07rem;
    }
    .mCd a{
        width:100%;
        height: 100%;
        display: block;
        text-align: center;
    }
    .mCd a img{
        margin: 0 auto;
        width:1rem;
    }
    .mCd a h3{
        font-size: .14rem;
        font-weight: normal;
        color:black;
        padding-top: .1rem;
        width:.9rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .mCd a span{
        padding-top: .08rem;
        font-size: .12rem;
        color:#aaa;
    }
    .pf{
        width:100%;
    }
    .icon {
        width: .8em;
        height: .8em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }
</style>


