<template>
    <div class="team">
        <h1>{{data.name}}</h1>
        <db-content v-for="n in data.items" :key="n.id" :data="n"></db-content>
    </div>
</template>

<script>
    import dbContent from "./dbGroupContent";
    export default {
        name: "dbGroupTeam",
        components:{
            dbContent
        },
        props:["data"]
    }
</script>

<style scoped>
    .team{
        padding-left:.18rem;
    }
    h1{
        padding-top:.38rem;
        padding-bottom:.2rem;
        font-size:.14rem;
        color:#aaaaaa;
        font-weight: normal;
    }
</style>


