<template>
    <div class="cdList">
        <div class="top">
            <h2>{{data.name}}</h2>
            <span v-show="data.cd?true:false">更多</span>
        </div>
        <db-newcd-dvd :data="data.cd" :size="size" v-show="data.cd?true:false"></db-newcd-dvd>
        <db-find :data="data.find" v-show="data.find?true:false"></db-find>
        <db-classify :data="data.classify" v-show="data.classify?true:false"></db-classify>
    </div>
</template>

<script>
    import dbNewcdDvd from "./dbNewcdDvd";
    import dbFind from "./dbFind";
    import dbClassify from "./dbClassify";
    export default {
        name: "dbNewcdClass",
        components:{
            dbNewcdDvd,
            dbFind,
            dbClassify
        },
        props:["data","size"]
    }
</script>

<style scoped>
    .top{
        display: flex;
        padding:.15rem .19rem .19rem .19rem;
        justify-content: space-between;
        align-items: center;
    }
    .top h2{
        font-size:.16rem;
        font-weight: normal;
    }
    .top span{
        font-size:.13rem;
        color:#67ba62;
    }
</style>


