<template>
    <div class="dbMy">
        <db-head></db-head>
        <section>
            <db-details></db-details>
            <db-items></db-items>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbDetails from "../components/dbMy/dbDetails";
    import dbItems from "../components/dbMy/dbItems";
    export default {
        name: "dbMy",
        components: {
            dbHead,
            dbDetails,
            dbItems
        }
    }
</script>

<style scoped>
    .dbMy{
        background-color: #f7f7f7;
        height:100%;
    }
</style>


