<template>
    <header>
        <h3 class="close" @click="go">关闭</h3>
        <input type="text" class="seach">
    </header>
</template>

<script>
    export default {
        name: "dbSeachHead",
        methods: {
            go(){
                this.$router.push({
                    path: "/",
                    auery:""
                })
            }
        }
    }
</script>

<style scoped>
    header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0 .18rem;
        height:.6rem;
        border-bottom: 1px solid #f3f3f3;
        border-top: 1px solid #b2b2b2;
    }
    .close{
        font-size: .14rem;
        font-weight: normal;
        color:#67ba62;
        line-height: .6rem;
    }
    .seach{
        box-sizing: border-box;
        -webkit-appearance: none;
        border:0;
        width:2.97rem;
        height:.32rem;
        background: url(../../assets/img/seachBg.png) no-repeat 50% #f3f3f3;
        background-size: 100% 100%;
        border-radius: 3px;
        font-size: 14px;
    }
</style>


