<template>
    <ul class="talkList">
        <li v-for="n in data">
            <a href="javascript:;">{{n}}</a>
        </li>
    </ul>
</template>

<script>
    export default {
        name: "dbGroupTalk",
        props:["data"]
    }
</script>

<style scoped>
    .talkList{
        padding:.1rem .18rem 0rem .18rem;
        display: flex;
        flex-wrap: wrap;
    }
    li{
        flex-shrink: 0;
        margin-bottom: .05rem;
    }
    li:nth-child(2n){
        margin-left: .05rem;
    }
    a{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size:.14rem;
        color:black;
        width:1.67rem;
        height:.48rem;
        background-color: #f6f6f6;
        border-radius: .05rem;

    }
</style>


