<template>
    <div class="group">
        <db-head></db-head>
        <section>
            <db-nav :data="groupData"></db-nav>
            <db-items :data="groupData.groups"></db-items>
            <db-foot></db-foot>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbFoot from "../components/commons/dbFoot";
    import dbNav from "../components/dbGroup/dbGroupNav";
    import dbItems from "../components/dbGroup/dbGroupItems";
    import dbGroupApi from "../apis/groupApi";
    export default {
        name: "dbGroup",
        components:{
            dbFoot,
            dbHead,
            dbNav,
            dbItems
        },
        data(){
            return{
                loading:false,
                groupData :[]
            }
        },
        created() {
            this._initPageData()
        },
        methods:{
            _initPageData(){
                this.loading = true;
                dbGroupApi.getPush(data=>{
                    this.groupData = data;
                    console.log(this.groupData);
                    this.loading = false;
                })
            }
        }
    }
</script>

<style scoped>

</style>


