<template>
    <div class="music">
        <db-head></db-head>
        <section>
            <db-newcd v-if="!loading" :data="musicData.music"></db-newcd>
            <db-foot></db-foot>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbNewcd from "../components/dbMusic/dbNewcd";
    import dbFoot from "../components/commons/dbFoot";
    import musicApi from "../apis/musicApi";
    export default {
        name : "dbMusic",
        data () {
            return {
                loading:false,
                musicData:[]
            }
        },
        created(){
            this._initPageData()
        },
        methods:{
            _initPageData(){
                this.loading = true;
                musicApi.getPush(data=>{
                    this.musicData = data;
                    this.loading = false;
                    console.log(this.musicData);
                })
            }
        },
        components: {
            dbHead,
            dbNewcd,
            dbFoot
        }
    }
</script>

<style scoped>
    .music{
        height:100%;
        display: flex;
        flex-direction: column;
    }
    section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }

</style>






