<template>
    <div>
        <db-seach-head></db-seach-head>
        <db-seach-menu :data="seachData.series"></db-seach-menu>
        <db-seach-nav></db-seach-nav>
    </div>
</template>

<script>
    import dbSeachHead from "../components/dbSeach/dbSeachHead";
    import dbSeachMenu from "../components/dbSeach/dbSeachMenu";
    import dbSeachNav from "../components/dbSeach/dbSeachNav";
    import seachApi from "../apis/seachApi";
    export default {
        name: "dbSeach",
        components: {
            dbSeachHead,
            dbSeachMenu,
            dbSeachNav
        },
        data(){
            return{
                seachData: []
            }
        },
        created() {
            this._initPageData()
        },
        methods: {
            _initPageData(){
                seachApi.getPush(data=>{
                    this.seachData = data;
                })
            }
        }
    }
</script>

<style scoped>

</style>


