<template>
    <section class="push">
        <div class="push-list" v-for="(n,i) in data">
            <h2 class="push-date">{{n.push.date}}</h2>
            <a href="javascript:;" class="push-item" v-show="n.push.author">

                <!--发送图片-->
                <div class="push-content img" v-show="n.push.pic.intro">
                    <div class="photos">
                        <div class="main" :style="{position: 'relative', background: 'url('+n.push.pic.picImg[0]+') center center / cover no-repeat rgb(250, 250, 250)'}"></div>
                        <div class="aside">
                            <div class="aside-pic" :style="{position: 'relative', background: 'url('+n.push.pic.picImg[1]+') center center / cover no-repeat rgb(250, 250, 250)'}"></div>
                            <div class="aside-pic" :style="{position: 'relative', background: 'url('+n.push.pic.picImg[2]+') center center / cover no-repeat rgb(250, 250, 250)'}"></div>
                        </div>
                    </div>
                    <h3 class="title">{{n.push.title}}</h3>
                </div>

                <!--发送文章-->
                <div class="push-content text" v-show="n.push.text.content">
                    <div class="cover">
                        <div class="cover-pic" :style="{position: 'relative', background: 'url('+n.push.text.textImg+') center center / cover no-repeat rgb(250, 250, 250)'}"></div>
                        <h3>{{n.push.title}}</h3>
                        <p>{{n.push.text.content}}</p>
                    </div>
                </div>

                <div class="author">
                    by <span class="name">{{n.push.author}}</span>
                </div>
            </a>
        </div>
    </section>
</template>

<script>
    export default {
        name: "dbIndexList",
        props:["data"]
    }
</script>

<style scoped>
    @import "../../assets/css/dbIndexList.css";
</style>


