<template>
    <section>
        <a href="javascript:;" v-for="(n,i) in data">
            <b :style="{'color':n.color}" @click="go(n.eng)">{{n.name}}</b>
            <span>{{n.title}}</span>
        </a>
    </section>
</template>

<script>
    export default {
        name: "dbSeachMenu",
        props: ["data"],
        methods:{
            go(name){
                this.$router.push({
                    path:'/'+name,
                    auery:''
                })
            }
        }
    }
</script>

<style scoped>
    section{
        width:100%;
        padding-top: 25px;
        display: flex;
        flex-wrap: wrap;
        border-bottom:1px solid #f3f3f3;
    }
    a{
        display: block;
        width:33.33%;
        display: block;
        text-align: center;
        margin-bottom: 25px;
    }
    b{
        display: block;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
    }
    span{
        display: block;
        color:#999;
        height:17px;
        font-size: 12px;
        letter-spacing: 1px;
    }
</style>


