<template>
    <div class="time">
        <db-head></db-head>
        <section>
            <db-slider v-if="!loading" :data="timeData.slider"></db-slider>
            <db-nav></db-nav>
            <db-shops v-if="!loading" :data="timeData.shops"></db-shops>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbSlider from "../components/dbTime/dbSlider";
    import dbNav from "../components/dbTime/dbNav";
    import dbShops from "../components/dbTime/dbShops";
    import timeApi from "../apis/timeApi";
    export default {
        name: "dbTime",
        components: {
            dbHead,
            dbSlider,
            dbNav,
            dbShops
        },
        data(){
            return {
                loading: false,
                timeData: []
            }
        },
        created() {
            this._initPageData()
        },
        methods: {
            _initPageData(){
                this.loading = true;
                timeApi.getPush(data=>{
                    this.timeData = data;
                    this.loading = false;
                    console.log(this.timeData);
                })
            }
        }
    }
</script>

<style scoped>
    .time{
        height:100%;
        background-color: #f7f7f7;
        display: flex;
        flex-direction: column;
    }
    section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }
</style>


