<template>
    <div class="concern">
        <a href="javascript:;">{{data.guanzhu}}<span>TA关注的人</span></a>
        <div class="con-pic">
            <div class="avatar"><img src="https://img3.doubanio.com/icon/up180441686-1.jpg"></div>
            <div class="avatar"><img src="https://img3.doubanio.com/icon/up180473770-1.jpg"></div>
            <div class="avatar"><img src="https://img3.doubanio.com/icon/up180473832-1.jpg"></div>
            <div class="avatar"><img src="https://img3.doubanio.com/icon/up179262119-2.jpg"></div>
        </div>
        <a href="javascript:;">{{data.bguanzhu}}<span>关注TA的人</span></a>
    </div>
</template>

<script>
    export default {
        name: "dbConcern",
        props: ["data"]
    }
</script>

<style scoped>
    .concern{
        display: flex;
        padding:.22rem .13rem;
        justify-content: space-between;
        background-color: white;
        margin-bottom: .15rem;
    }
    .concern a{
        display: block;
        text-align: center;
        font-size:.11rem;
        color:black;
    }
    .concern a span{
        color:#9f9f9f;
    }
    .concern a:last-child{
        border-left: 1px solid #e8e8e8;
        padding-left: .26rem;
    }
    .con-pic{
        flex: 1;
        padding-left: .16rem;
        display: flex;
    }
    .avatar{
        width: 26px;
        height: 30px;
        overflow: visible;
    }
    .con-pic img{
        width: .30rem;
        height:.30rem;
        border-radius: 50%;
        border:1px solid white;
        position: relative;
    }
</style>


