<template>
    <ul class="nav">
        <li><a href="javascript:;" @click="goMovie">影院热映</a></li>
        <li><a href="javascript:;" @click="go">我的</a></li>
        <li><a href="javascript:;" @click="goTime">豆瓣时间</a></li>
        <li><a href="javascript:;">使用豆瓣App</a></li>
    </ul>
</template>

<script>
    export default {
        name: "dbIndexNav",
        methods: {
            go(){
                this.$router.push({
                    path: "/my",
                    auery:""
                })
            },
            goMovie(){
                this.$router.push({
                    path: "/movie",
                    auery:""
                })
            },
            goTime(){
                this.$router.push({
                    path:"/time",
                    auery:""
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../assets/css/dbIndexNav.css";

</style>


