<template>
    <div class="details">
        <a class="lef-tx">
            <img src="https://img3.doubanio.com/icon/ul141028592-2.jpg">
        </a>
        <div class="rig-dt">
            <div class="dt-top">
                <h3>知野</h3>
                <div class="dt-zhongxin">
                    <b>ID:<span class="id">666888999</span></b> <span class="zhuye" @click="go">个人主页 &gt; </span>
                </div>
            </div>
            <div class="dt-down">
                <div class="guanzhu">
                    关注 <span>4</span>
                </div>
                <div class="guanzhu b">
                    被关注 <span>3721</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dbDetails",
        methods: {
            go(){
                this.$router.push({
                    path: "/people",
                    auery: ""
                })
            }
        }
    }
</script>

<style scoped>
    .details{
        width:100%;
        height:1.24rem;
        background-color: black;
        display: flex;
        align-items: center;
        margin-bottom: .15rem;
    }
    .lef-tx{
        margin:0 .11rem 0 .2rem;
        width:.74rem;
        height:.74rem;
        border:4px solid white;
        border-radius: 50%;
        overflow: hidden;
    }
    .lef-tx img{
        width:.74rem;
        height:.74rem;
    }
    .rig-dt{
        flex: 1;
    }
    .rig-dt .dt-top h3{
        font-size:.18rem;
        color:white;
        font-weight:normal;
    }
    .dt-zhongxin{
        font-size:.13rem;
        color:white;
        display: flex;
        justify-content: space-between;
    }
    .dt-zhongxin .zhuye{
        padding-right: .16rem;
    }
    .dt-zhongxin b{
        display: flex;
        font-weight: normal;
    }
    .dt-top{
        border-bottom: 1px solid #3b3c40;
        padding-bottom: .1rem;
    }
    .dt-down{
        display: flex;
        font-size: .13rem;
        color:white;
        padding-top:.12rem;
    }
    .guanzhu{
        display: flex;
    }
    .b{
        margin-left:.24rem;
    }
    .guanzhu span{
        padding-left:.05rem;
    }

</style>


