<template>
    <div class="life">
        <h3>2019</h3>
        <div class="lifeInfo">
            <div class="looked">
                <div class="looked-top">
                    <h3>看过</h3>
                    <span>{{data.looked[0].lTime}}</span>
                </div>
                <div class="looked-menu">
                    <div class="cover" :style="{backgroundImage: 'url('+data.looked[0].pic+')'}"></div>
                    <div class="detail">
                        <h4>{{data.looked[0].mName}}</h4>
                        <p>{{data.looked[0].mark}}</p>
                        <span>{{data.looked[0].mTime}}</span>
                    </div>
                </div>
            </div>
            <div class="team" v-for="(n,i) in data.team">
                <div class="looked-top">
                    <h3>加入小组</h3>
                    <span>{{n.tTime}}</span>
                </div>
                <div class="looked-menu teamList">
                    <div class="wenzi">
                        <h4>{{n.title}}</h4>
                        <p>{{n.content}}</p>
                    </div>
                    <div class="cover" :style="{backgroundImage: 'url('+n.tPic+')'}"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dbLife",
        props:["data"]
    }
</script>

<style scoped>
    .life{
        background-color: white;
    }
    .life h3{
        font-size: .11rem;
        text-align: center;
        padding:.16rem 0;
        color:#aaaaaa;
        font-weight:normal;
        background-color: #f5f5f5;
    }
    .lifeInfo{
        background-color: white;
        padding-left: .16rem;
    }
    .looked{
        padding-bottom: .35rem;
        border-bottom:1px solid #e8e8e8;
    }
    .looked-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .looked-top h3{
        font-size:.13rem;
        color:#aaa;
        background-color: white;
    }
    .looked-top span{
        font-size: .13rem;
        color:#aaa;
        padding-right: .2rem;
    }
    .looked-menu{
        padding:.15rem;
        background-color: #f9f9f9;
        display: flex;
    }
    .cover{
        width:75px;
        height:111px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    .detail{
        padding-left: .15rem;
    }
    .detail h4{
        font-size: .17rem;
        font-weight: normal;
    }
    .detail p{
        font-size:.11rem;
        color:#aaa;
        line-height: .32rem;
    }
    .detail span{
        display: block;
        font-size:.11rem;
        color:#aaa;
    }
    .team{
        padding-bottom: .35rem;
        border-bottom:1px solid #e8e8e8;
    }
    .teamList{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .teamList h4{
        font-size: .17rem;
        font-weight: normal;
    }
    .teamList p{
        font-size:.11rem;
        color:#aaa;
        line-height: .15rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical
    }
    .teamList .cover{
        width: 75px;
        height: 48px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    .wenzi{
        width:2.14rem;
    }
</style>


