<template>
    <ul class="type-list">
        <li v-for="n in data">
            <a href="javascript:;">
                {{n}}
                <span>&gt;</span>
            </a>
        </li>

    </ul>
</template>

<script>
    export default {
        name: "dbClassify",
        props:["data"]
    }
</script>

<style scoped>
    .type-list{
        color: #eee;
        font-size: .9rem;
        overflow: hidden;
        display: flex;
        flex-shrink: 0;
        flex-wrap: wrap;
        padding-left:.14rem;
        border-bottom: 1px solid #eee;
    }
    li{
        width: 1.64rem;
        border-top: solid 1px #eee;
        border-right: solid 1px #eee;
        float: left;
        height:.42rem;
        display: flex;
        justify-content: space-between;
        /*padding-right: 1.12rem;*/
        /*height: 2.64rem;*/
    }
    li:nth-child(2n){
        border-right: 0;
        padding-left: .14rem;
    }
    a{
        width:100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #42bd56;
        font-size:.14rem;
    }
    span{
        color:#aaa;
        margin-right:.09rem;
    }
    li:nth-child(2n) a span{
        margin-right:0;
    }
</style>


