<template>
    <header>
            <h1 @click="goHome">豆瓣</h1>
            <img src="../../assets/img/headIcon.png" @click="go">
        <a href="javascript:;" class="openapp">
            打开豆瓣App
        </a>
    </header>
</template>

<script>
    export default {
        name: "dbHead",
        methods: {
            go(){
                this.$router.push({
                    path:"/seach",
                    auery:""
                })
            },
            goHome(){
                this.$router.push({
                    path:"/",
                    auery:""
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../assets/commons/dbHead.css";
</style>


