<template>
    <div class="banner">
        <div class="banner-top">
            <img src="https://img3.doubanio.com/icon/up141028592-2.jpg" class="tx">
        </div>
        <div class="banner-mid">
            <h3>{{data.name}}<img src="../../assets/img/people.png"></h3>
            <p>西安</p>
        </div>
        <div class="banner-down">
            <a href="javascript:;">{{data.teamNum}}<span>小组</span></a>
            <a href="javascript:;">{{data.radio}}<span>广播</span></a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dbBanner",
        props:["data"]
    }
</script>

<style scoped>
    .banner{
        margin-bottom: .15rem;
        background-color: white;
    }
    .banner-top{
        height:1.92rem;
        background-color: #17191d;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .tx{
        width:1.02rem;
        height:1.02rem;
        border-radius:50%;
    }
    .banner-mid{
        padding:.23rem .18rem .16rem .18rem;
        border:1px solid #e8e8e8;
    }
    .banner-mid h3{
        font-size:.19rem;
        font-weight:normal;
    }
    .banner-mid h3 img{
        width:.12rem;
        height:.18rem;
        display: inline-block;
    }
    .banner-mid p{
        font-size: .12rem;
        color:#9b9b9b;
    }
    .banner-down{
        display: flex;
        padding:.23rem 0 .2rem .2rem;
    }
    .banner-down a{
        display: block;
        font-size: .11rem;
        text-align: center;
        color:black;
    }
    .banner-down a span{
        color:#9b9b9b;
    }
    .banner-down a:first-child{
        margin-right:.58rem;
    }
</style>


