<template>
    <div class="watch">
        <db-head></db-head>
        <section>
            <db-newcd v-if="!loading" :data="watchData.watch" :size="size"></db-newcd>
            <db-foot></db-foot>
        </section>
    </div>
</template>

<script>
    import dbHead from "../components/commons/dbHead";
    import dbFoot from "../components/commons/dbFoot";
    import dbNewcd from "../components/dbMusic/dbNewcd";
    import watchApi from "../apis/watchApi";
    export default {
        name: "dbWatch",
        components:{
            dbHead,
            dbFoot,
            dbNewcd
        },
        data(){
            return {
                loading:false,
                watchData:[],
                size:''
            }
        },
        created(){
            this._initPageData(),
            this.getSize()
        },
        methods:{
            _initPageData(){
                this.loading = true;
                watchApi.getPush(data=>{
                    this.watchData = data;
                    this.loading = false;
                    console.log(this.watchData);
                })
            },
            getSize(){
                if(this.watchData.title="bigSize"){
                    this.size=150
                }
            }
        }
    }
</script>

<style scoped>
    .watch{
        height:100%;
        background-color: white;
        display: flex;
        flex-direction: column;
    }
    section{
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
    }
</style>


