<template>
    <div class="shops">
        <div class="today">
            <h3>{{data[0].title}}</h3>
            <div class="menu">
                <a href="javascript:;" v-for="n in data[0].goods">
                    <img :src="n.pic">
                    <h2>{{n.name}}</h2>
                    <div class="price">{{n.price}}.00 <span>元</span></div>
                    <div class="price quoP" v-show="n.quoPri">{{n.quoPri}}.00元</div>
                </a>
            </div>
        </div>
        <div class="now">
            <div class="top">
                <h3>{{data[1].title}}</h3>
                <p>查看更多 &gt;</p>
            </div>
            <ul class="now-list">
                <li v-for="n in data[1].goods">
                    <a href="javascript:;"><img :src="n.pic"></a>
                    <div class="now-menu">
                        <div class="now-menu-top">
                            <h2 class="now-h2">{{n.name}}</h2>
                            <p class="price">{{n.price}}.00 <span>元</span></p>
                        </div>
                        <i>主讲人 <span>{{n.speaker}}</span></i>
                        <p class="title">{{n.title}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "dbShops",
        props:["data"]
    }
</script>

<style scoped>
    .today{
        padding:0 .2rem .21rem .2rem;
        background-color: white;
        margin-bottom: .15rem;
    }
    .today h3{
        font-size: .18rem;
        padding:.1rem 0;
    }
    .menu{
        display: flex;
        justify-content: space-between;
    }
    .menu a {
        width:1.04rem;
    }
    .menu a img{
        width:100%;
        border-radius:3px;
    }
     h2{
        font-size: 15px;
        line-height: 18px;
        color: #191919;
        margin: 10px 0;
        height: 36px;
        font-weight: 700;
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        white-space: normal;
        word-wrap: break-word;
    }
    .price{
        font-size:.12rem;
        font-weight: bolder;
        color:#e8803e;
    }
    .price span{
        display: inline-block;
    }
    .quoP{
        color:#cccccc;
        font-weight: normal;
        text-decoration: line-through;
    }
    .now{
        padding:0 .2rem;
        background-color: white;
    }
    .top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: .1rem;
    }
    .top h3{
        font-size:.18rem;
    }
    .top p{
        font-size:.12rem;
    }
    .now-list li{
        display: flex;
        padding: .19rem 0;
        border-bottom: 1px solid #dfdfdf;
    }
    .now-list li a{
        width:1rem;
        margin-right:.15rem;
    }
    .now-list li a img{
        width:100%;
        border-radius: 3px;
    }
    .now-menu{
        display: flex;
        flex-direction: column;
    }
    .now-h2{
        width:167px;
        margin:0;
    }
    .now-menu-top{
        display: flex;
    }
    .now-menu i{
        font-size:.12rem;
        color:#818181;
        margin: 17px 0;

    }
    .now-menu i span{
        display: inline-block;
    }
    .now-menu .title{
        font-size:.1rem;
        color:#818181;
    }
</style>


